@import "../bootstrap/functions";
@import "../bootstrap/variables"; 
@import "../bootstrap/mixins";

/////////
//COLOR//
/////////

$green: #11a847 !default;
$powder-green: #eaf4e5 !default;
$gradient-green: linear-gradient(to bottom, #51be77, #27ae58) !default; 
$gradient-yellorange: linear-gradient(to bottom, #ffa81a, #ffa81a) !default; 

///
$color-primary: $green !default;
$color-secondary: $powder-green !default;

$navbar-padding:0;
$navbar-md-padding:0;
$navbar-lg-padding:0;
$navbar-xl-padding:0;

$navbar-mega-menu-bg: $color-secondary !default;
$navbar-mega-menu-lg-bg: white !default;


$navbar-navlink-color:#333 !default;
$navbar-navlink-hover-color: $color-primary !default;


$navbar-navlink-bg-color:#333 !default;
$navbar-navlink-lg-color:#333 !default;
$navbar-navlink-lg-bg-color:#333 !default;

$navbar-navlink-padding: 20px 50px 20px 30px !default;