

$navbar-default:(
  xs:(

  ),
  lg:(
    display: flex,
    flexDirection: column,
    boxShadow: 0px 3px 8px rgba(2, 2, 2, 0.15)
  )
) !default;


//make navbar brand function
$navbar-brand:(
  xs:(
    width: round(399px * 0.3),
    height: round(67px * 0.3),
    backgroundImage: url('data:image/png;base64,'),
    backgroundSize:round(399px * 0.3) round(67px * 0.3),
    marginRight:.75rem,
    marginLeft:1rem
  ),
  lg:(

    backgroundImage: url(data:image/svg+xml;base64,),
    backgroundSize:191px 39px,
    width: 191px,
    height: 39px,
    marginRight:1rem,
    marginLeft:0
  )
) !default;


$navbar-top-menu:(
  xs:(
    height:50px,
    position:fixed,
    top:0,
    left:0,
    zIndex:1002,
    // bgColor:rgba(0,0,0,.2),
    bgColor:#00994e,
    borderBottomColor:rgba(0,0,0,0),
    InsideContainer:(
      height: 100%,
      display:flex,
      justifyContent: center,
      alignItems: center,
    ),
    UnitMenu:(
      display: none
    )
  ),
  lg:(
    position:relative,
    top:0,
    left:0,
    height:80px,
    bgColor:#fff,
    borderBottomColor:#00994e,
    InsideContainer:(
      height: 100%,
      display:flex,
      justifyContent: space-between,
      alignItems: center,
    ),
    UnitMenu:(
      display: flex
    )
  )
) !default;

$navbar-mega-menu:(
  xs:(
    position:fixed,
    top:0,
    left:0,
    display:none,
    height: 100%,
    width:85%,
    padding:50px 0 50px 0,
    zIndex:1001,
    overflowY:scroll,
    bgColor:#f8fbf7,
    borderRight: 1px solid #ddd,
    NavBarWidth:100%,
    NavBarBgColor:$powder-green,
    InsideContainer:(
      display:flex,
      flexDirection:column,
      justifyContent: center,
      alignItems: center,
      padding:0,
      position: relative
    ),
    UnitMenu:(
      display: flex
    ),
    navItem:(
      borderBottom:solid 1px #dfdfdf
    ),
    navlink:(
      color:$navbar-navlink-color,
      hoverColor:$navbar-navlink-hover-color,
      padding:$navbar-navlink-padding,
      justifyContent:flex-start
    )
  ),
  md:(
    width:85%,
    InsideContainer:(),
    UnitMenu:(),
    navItem:(),
    navlink:()
  ),
  lg:(
    position:relative,
    top:0,
    left:0,
    maxHeight:none,
    minHeight:auto,
    width:100%,
    overflowY:visible,
    bgColor:#fff,
    display:block,
    padding:0,
    NavBarBgColor:transparent,
    NavBar:(
      bgColor:transparent
    ),
    InsideContainer:(
      display:flex,
      flexDirection:row,
      justifyContent: space-between,
      alignItems: center,
      padding:0
    ),
    UnitMenu:(
      display: none !important
    ),
    navItem:(
      borderBottom:none,
      borderLeft:solid 1px #dfdfdf,
      maxWidth:215px
    ),
    navlink:(
      height: 47px,
      padding: 10px,
      color:$navbar-navlink-color,
      hoverColor:$navbar-navlink-hover-color,
      justifyContent:center
    )
  )
) !default;


$navbar-unit-menu:(
  xs:(
    padding:1rem 0,
    navItem:(
      padding:0 15px,
      position: relative
    ),
    navlink:(
      position: relative,
      fontSize:15px,
      padding:2.5px 30px !important,
      color:$navbar-navlink-color,
      hoverColor:$navbar-navlink-hover-color,
      justifyContent:flex-start,
      hover-color:$color-primary,
      active-color:$color-primary,
      active-arrows:none,
      arrows:none
    )
  ),
  lg:(
    navItem:(

    ),
    navlink:(
      fontSize:14px,
      padding: 0 15px !important,
      color:#444,
      hoverColor:$navbar-navlink-hover-color,
      justifyContent:center,
      active-arrows:block
    )
  )
) !default;


//dropdowns

$navbar-mega-dropdown:(
  xs:(
    hover-before:none,
    hover-after:none,
    container:(
      display: flex,
      flexDirection:column
    ),
    dropdownMenu:(
      border:none,
      boxShadow:none,
      padding:0,
      container:(
        padding:0,
      ),
    ),
    dropdownMenuInner:(
      dispaly:none,
      ul:(
      ),
      li:(
        margin:0 0 0 40px,
        borderTop:1px solid #ddd,
        lastChildBorderBottom:0,
        extraNavbarMarginLeft:auto
      ),
      dropdownSubmenu:(
        
        display:none,
        border:none,
        padding:0,
        margin:0 0 0 35px,
        backgroundImage:none,
        backgroundColor:transparent,
        // borderBottom:1px solid #ddd,
        visibility:hidden,
        opacity: 1,
        li:(
           
        ),
        dropdownItem:(
          padding:0px 0px 0px 0px,
          fontSize:15px,
          first-paddingTop:15px,
          last-paddingBottom:15px,
          hover-color:$color-primary,
          hover-background:transparent
        )
      ),
      dropdownTitle:(
        textDecoration: none,
        display: block,
        padding:20px 0,
        lineHeight:1,
        letterSpacing:1px,
        whiteSpace:nowrap,
        color:#444
      ),
      dropdownItem:(
        textDecoration:none,
        fontSize:14px
      )
    ),
    dropdownMenuAction:(
      li:(

      ),
      dropdownItem:(
        display:flex,
        textDecoration: none,
        alignItems: center,
        padding:10px 30px,
        borderBottom:1px solid #ddd
      )
    )
  ),
  lg:(
    hover-borderTop:4px solid #ffa81a,
    hover-marginTop:-4px,
    hover-before:block,
    hover-after:block,
    hover-menu:block,
    container:(
      flexDirection:row
    ),
    dropdownMenu:(
      position:absolute,
      backgroundColor:#eaf4e5,
      padding:20px 0,
      paddingLeft:0,
      // backgroundImage:linear-gradient(to right, #edf3e9, #ffffff),
      boxShadow:0 0px 8px rgba(2, 2, 2, .15),
      display: none,
      container:(
        padding:0,
      ),
    ),
    dropdownMenuInner:(
      display:flex,
      flex:1 1 auto,
      width:calc(100%),
      padding:0 15px,
      extraNavbarMinWidth:180px,
      li:(
        margin:0,
        padding: 0 15px,
        width: calc(100% / 7),
        maxWidth:14%,
        width1: calc(100% / 1),
        width2: calc(100% / 2),
        width3: calc(100% / 3),
        width4: calc(100% / 4),
        width5: calc(100% / 5),
        width6: calc(100% / 6),
        borderTop:0
      ),
      dropdownSubmenu:(
        display:block,
        transform:translateY(0),
        position: static,
        backgroundColor:transparent,
        borderBottom:none,
        visibility: unset,
        margin:0 ,
        li:(
          padding:.25rem 0
        ),
        dropdownItem:(
          padding:0,
          fontSize:14px,
          first-paddingTop:0,
          last-paddingBottom:0,
        )
      ),
      dropdownTitle:(
        fontSize: 19px,
        padding:0 0 1rem 0,
        marginBottom:10px,
        color:$green,
        borderBottom:1px solid $color-primary
      ),
      dropdownItem:(
        textDecoration:none,
        fontSize: 14px,
      )
    ),  
    dropdownMenuAction:(
      width:16%,
      display: flex,
      flexDirection: column,
      li:(
        display:flex,
        flex:1 1 auto,
        justifyContent:center,
        alignItems: center,
        padding:0.25rem 0,
      ),
      dropdownItem:(
        display: flex,
        textDecoration:none,
        fontSize:14px,
        color:$color-primary,
        alignItems: center,
        padding:0.25rem .5rem,
        height: 50px,
        borderRaduis:30px,
        backgroundColor: #edf3e9,
        hover-color:#fff,
        hover-backgroundImage:linear-gradient(to bottom, #51be77, #27ae58),
      )
    )
  )
) !default;
