.cli-wrap {
    box-sizing: border-box;
	*,
	*::before,
	*::after {
	  box-sizing: border-box;
	}

	margin: 0; // 1
	font-family: $font-family-base;
	@include font-size($font-size-base);
	font-weight: $font-weight-base;
	line-height: $line-height-base;
	color: $body-color;
	text-align: left; // 3
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba($black, 0);
	font-size:16px;

	ul{
		margin:0;
	}
	.container {
		@include make-container();
		@include make-container-max-widths();
		&:before,&:after{
			display: none;
		}
	}

	.container-fluid {
	  width: 100%;
	  padding-right: 15px;
	  padding-left: 15px;
	  margin-right: auto;
	  margin-left: auto; }

	.row {
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  -ms-flex-wrap: wrap;
	      flex-wrap: wrap;
	  margin-right: -15px;
	  margin-left: -15px; }

	.col,.col-auto{
	  position: relative;
	  width: 100%;
	  padding-right: 15px;
	  padding-left: 15px; }

	.col {
	  -ms-flex-preferred-size: 0;
	      flex-basis: 0;
	  -webkit-box-flex: 1;
	      -ms-flex-positive: 1;
	          flex-grow: 1;
	  max-width: 100%; }

	.col-auto {
	  -webkit-box-flex: 0;
	      -ms-flex: 0 0 auto;
	          flex: 0 0 auto;
	  width: auto;
	  max-width: 100%; }


	//////////////////////// dropdown //////////////////////////
	.dropdown {
	  position: relative; }

	.dropdown-toggle {
	  white-space: nowrap;
	  &:after{
	  	content:'';
	  	display: none;
	  }
	  // Generate the caret automatically
	  // @include caret;
	}

	// The dropdown menu
	.dropdown-menu {
	  position: absolute;
	  top: 100%;
	  left: 0;
	  z-index: $zindex-dropdown;
	  display: none; // none by default, but block on "open" of the menu
	  float: left;
	  // min-width: $dropdown-min-width;
	  padding: 20px 0;
	  margin: $dropdown-spacer 0 0; // override default ul
	  @include font-size($dropdown-font-size);
	  color: $dropdown-color;
	  text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)
	  list-style: none;
	  background-color: $dropdown-bg;
	  background-clip: padding-box;
	  border: $dropdown-border-width solid $dropdown-border-color;
	  background-image: linear-gradient(to right, #edf3e9, #ffffff);
	  border-top: 0;
	  @include border-radius($dropdown-border-radius);
	  @include box-shadow($dropdown-box-shadow);
	}


	// Links, buttons, and more within the dropdown menu
	//
	// `<button>`-specific styles are denoted with `// For <button>s`
	.dropdown-item {
	  display: block;
	  width: 100%; // For `<button>`s
	  clear: both;
	  font-weight: $font-weight-normal;
	  color: $dropdown-link-color;
	  text-align: inherit; // For `<button>`s
	  // white-space: nowrap; // prevent links from randomly breaking onto new lines
	  background-color: transparent; // For `<button>`s
	  border: 0; // For `<button>`s
	  padding: 0;
	  

	  @include hover-focus {
	    color: $dropdown-link-hover-color;
	    text-decoration: none;
	    //@include gradient-bg($dropdown-link-hover-bg);
	  }

	  // &.active,
	  // &:active {
	  //   color: $dropdown-link-active-color;
	  //   text-decoration: none;
	  //   @include gradient-bg($dropdown-link-active-bg);
	  // }

	  &.disabled,
	  &:disabled {
	    color: $dropdown-link-disabled-color;
	    pointer-events: none;
	    background-color: transparent;
	    // Remove CSS gradients if they're enabled
	    @if $enable-gradients {
	      background-image: none;
	    }
	  }
	}

	// .dropdown-menu.show {
	//   display: block; }



	@media (min-width: map-get($grid-breakpoints, 'lg')) {
	  .dropdown {
	    .dropdown-menu {
	      display: block;
	      visibility: hidden;
	      opacity: 1;
	      // transform: translateY(20px);
	      // transition: opacity 0.3s ease-in;
	      &:after{
	      	display: block;
	      	content: "";
	      	position: absolute;
	      	width: 100%;
	      	height: 30%;
	      }
	    }
	    &:hover > .dropdown-menu {
	      visibility: visible;
	      // opacity: 1;
	      // transform: scaleY(1);
	      // transform: translateY(0px);
	      // transition: transform 0.3s ease-out;
	      //ul{display: flex;}
	      .mega-dropdown-menu-inner ul{
	      	visibility: visible;
	      }
	    }
	  }
	}

	//////////////////////// dropdown end //////////////////////////

	//////////////////////// megamenu /////////////////////////


	.mega-dropdown {
	  position: static;
	  display: block;
	}

	.mega-dropdown-menu {
	  	width: 100%;
	 	position: absolute;
	  	top:100%;
	  	box-shadow: 0 0px 8px rgba(2, 2, 2, .15);
		>.container{
			display: flex;
		}
	}

	// mobile menu css
	$icon-bar-color:$white;
	.icon-bar {
	  width: 30px;
	  height: 2px;
	  background-color: $icon-bar-color;
	  display: block;
	  -webkit-transition: all 0.2s;
	  transition: all 0.2s;
	  margin-top: 6px;
	  &:first-child{
	  	margin-top: 0;
	  }
	}

	.navbar-toggler {
	  &:focus {
	    outline: 0px;
	  }
	  .top-bar {
	    transform: rotate(45deg);
	    transform-origin: 15% 190%;
	    background-color: $icon-bar-color;
	  }
	  .middle-bar {
	    opacity: 0;
	  }
	  .bottom-bar {
	    transform: rotate(-45deg);
	    transform-origin: 15% -80%;
	    background-color: $icon-bar-color;
	  }
	  &.collapsed {
	    .top-bar {
	      transform: rotate(0);
	      background-color: $icon-bar-color;
	    }
	    .middle-bar {
	      opacity: 1;
	    }
	    .bottom-bar {
	      transform: rotate(0);
	      background-color: $icon-bar-color;
	    }
	  }
	}
	//////////////////////// megamenu end /////////////////////////

	//////////////////////// navbar  //////////////////////////
	// Navbar
	//
	// Provide a static navbar from which we expand to create full-width, fixed, and
	// other navbar variations.

	.navbar {
	  position: relative;
	  display: flex;
	  flex-wrap: wrap; // allow us to do the line break for collapsing content
	  align-items: center;
	  justify-content: space-between; // space out brand from logo
	  padding: $navbar-padding-y $navbar-padding-x;

	  // Because flex properties aren't inherited, we need to redeclare these first
	  // few properties so that content nested within behave properly.
	  > .container,
	  > .container-fluid {
	    display: flex;
	    flex-wrap: wrap;
	    align-items: center;
	    justify-content: space-between;
	  }
	}


	// Navbar brand
	//
	// Used for brand, project, or site names.

	.navbar-brand {
	  display: inline-block;
	  margin-top: $navbar-brand-padding-y;
	  margin-bottom: $navbar-brand-padding-y;
	  // margin-right: $navbar-padding-x;
	  // @include font-size($navbar-brand-font-size);
	  // line-height: inherit;
	  // display: block;
	  // white-space: nowrap;
	  text-indent: -99999px;
	  // @include hover-focus {
	  //   text-decoration: none;
	  // }
	  background-size: 100% 100%;
	  background-repeat: no-repeat;
	}


	// Navbar nav
	//
	// Custom navbar navigation (doesn't require `.nav`, but does make use of `.nav-link`).

	.navbar-nav {
	  display: flex;
	  flex-direction: column; // cannot use `inherit` to get the `.navbar`s value
	  padding-left: 0;
	  margin-bottom: 0;
	  list-style: none;

	  .nav-link {
	    text-decoration: none;
	  }

	  .dropdown-menu {
	    position: static;
	    float: none;
	  }
	}

	// Responsive navbar
	//
	// Custom styles for responsive collapsing and toggling of navbar contents.
	// Powered by the collapse Bootstrap JavaScript plugin.

	// When collapsed, prevent the toggleable navbar contents from appearing in
	// the default flexbox row orientation. Requires the use of `flex-wrap: wrap`
	// on the `.navbar` parent.
	.navbar-collapse {
	  flex-basis: 100%;
	  flex-grow: 1;
	  // For always expanded or extra full navbars, ensure content aligns itself
	  // properly vertically. Can be easily overridden with flex utilities.
	  align-items: center;
	}

	// Button for toggling the navbar when in its collapsed state
	.navbar-toggler {
	  padding: $navbar-toggler-padding-y $navbar-toggler-padding-x;
	  @include font-size($navbar-toggler-font-size);
	  line-height: 1;
	  background-color: transparent; // remove default button style
	  border: 0 solid transparent; // remove default button style
	  @include border-radius($navbar-toggler-border-radius);

	  @include hover-focus {
	    text-decoration: none;
	  }
	}

	// Keep as a separate element so folks can easily override it with another icon
	// or image file as needed.
	.navbar-toggler-icon {
	  display: inline-block;
	  width: 1.5em;
	  height: 1.5em;
	  vertical-align: middle;
	  content: "";
	  background: no-repeat center center;
	  background-size: 100% 100%;
	}

	@media (max-width: 991.98px) {
	  .navbar-expand-lg > .container,
	  .navbar-expand-lg > .container-fluid {
	    padding-right: 0;
	    padding-left: 0; } }

	@media (min-width: map-get($grid-breakpoints, 'lg')) {
	  .navbar-expand-lg {
	    -webkit-box-orient: horizontal;
	    -webkit-box-direction: normal;
	        -ms-flex-flow: row nowrap;
	            flex-flow: row nowrap;
	    -webkit-box-pack: start;
	        -ms-flex-pack: start;
	            justify-content: flex-start; }
	    .navbar-expand-lg .navbar-nav {
	      -webkit-box-orient: horizontal;
	      -webkit-box-direction: normal;
	          -ms-flex-direction: row;
	              flex-direction: row; }
	      // .navbar-expand-lg .navbar-nav .dropdown-menu {
	      //   position: absolute; }
	      // .navbar-expand-lg .navbar-nav .nav-link {
	      //   padding-right: 0.5rem;
	      //   padding-left: 0.5rem; }
	    .navbar-expand-lg > .container,
	    .navbar-expand-lg > .container-fluid {
	      -ms-flex-wrap: nowrap;
	          flex-wrap: nowrap; }
	    .navbar-expand-lg .navbar-collapse {
	      display: -webkit-box !important;
	      display: -ms-flexbox !important;
	      display: flex !important;
	      -ms-flex-preferred-size: auto;
	          flex-basis: auto; }
	    .navbar-expand-lg .navbar-toggler {
	      display: none; } }


	//////////////////////// navbar end //////////////////////////

	//////////////////////// transition //////////////////////////
	.fade {
	  @include transition($transition-fade);

	  &:not(.show) {
	    opacity: 0;
	  }
	}

	.collapse {
	  &:not(.show) {
	    display: block;
	    transform:translateX(-100%);
	    transition: transform .2s ease-in;
	  }
	  &.show{
	  	transform:translateX(0);
	  }
		@media (min-width: map-get($grid-breakpoints, 'lg')) {
		  &:not(.show) {
		    display: block;
		    transform:translateX(0);
		    transition:none;
		  }
		}
	}


	.collapsing {
	  //position: relative;
	  //height: 0;
	  //overflow: hidden;
	  transition: transform .2s ease-in;
	  //@include transition($transition-collapse);
	}

	//////////////////////// transition end //////////////////////////
}
