.mask-on{
	body{
		&:before{
			display:block;
			content:'';
			position:fixed;
			top:0;
			left:0;
			// right:0;
			// bottom:0;
			width: 100vw;
    		height: 150vh;
			background-color:rgba(0,0,0,.5);
			z-index:999;
		}
	}
}

.body-lock{
	body{
		position:fixed;
		top:0;
		width: 100%;
	}
}
main,.cathay-main{
	padding-top: 50px;
	@media (min-width: map-get($grid-breakpoints, 'lg')){
		padding-top:0px;
	}
}
body.sticky{
	>main,.cathay-main{
		padding-top: 127px;
	}
}
.cli-header{
	ul{
		margin: 0;
		padding:0;
		list-style:none;
	}

	position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
    box-sizing: border-box;
	@media (min-width: map-get($grid-breakpoints, 'lg')){
		position: relative;
	}
	.navbar-mega-menu .img-icon{
		display: none;
	}
	.nav-icon-link{
		width: 100%;
		display:flex;
		flex-wrap: wrap;
		border:0;
		justify-content: center;
		a{
			color:#333;
			text-decoration: none;
			font-size:14px;
			line-height:1;
		}
		img{
			width: 30px;
			height:30px;
			margin:0 auto;
			display:block;
		}

		li{
			flex: 1;
			padding:21px 0;
			text-align:center;
			&:nth-child(3n+2){
				border-left:1px solid #dfdfdf;
				border-right:1px solid #dfdfdf;
			}
			&:nth-child(n+4){
				border-top:1px solid #dfdfdf;
			}
		}
		p{
			margin:5px 0 0 0;
			padding:0 5px;
		}

		+.nav-icon-link{
			border-top:1px solid #dfdfdf;
		}

	}
	.nav-social-link{
		width: 100%;
		display:flex;
		justify-content: center;
		padding:20px 0px;
		border-top:1px solid #dfdfdf;
		a{
			line-height:1;
			padding:0px 10px;
			display:block;
		}
		img{
			width: 40px;
			height:40px;
			margin:0 auto;
			display:block;
		}
		li{
			// flex: 0 1 40px;
			text-align:center;
			display: block;


			@media (min-width: map-get($grid-breakpoints, 'lg')){
			}
		}

	}
    .extra-navbar {
		display: flex;
		align-items: flex-start;		
		flex-direction: row;
  		flex-wrap: wrap;
		width: 100%;
	}
	.extra-nav-link {
		display: flex;
		align-items: center;
		flex-direction: row;
		flex:0 0 46%;
		margin: 5% 0;
		height: 60px;
		border-radius: 30px;
		height: 45px;
		text-decoration: none;
	    padding: 0px 10px;
		background-image: linear-gradient(to bottom, #51be77, #27ae58);
		&:hover {
			background-color: #fff;
			cursor: pointer;
			text-decoration: none;
			p {
			color: #00994e;
			}
		}
		.iconSpan {
			&:before {
				content: '';
				margin: 5px;
				width: 30px;
				height: 30px;
				position: absolute;
				background-size: contain;
			}
		}
		p {
			// word-break: keep-all;
			font-size: 14px;
			color: #fff;
			margin:0;
		}
	}
	@media (min-width: map-get($grid-breakpoints, 'lg')){
		.extra-navbar {
			margin-left: auto;
			// align-items: center;
		}
		.extra-nav-link {
			flex:0 1 100%;
			height: 50px;
			background-color: #fff;
			background-image:none;
			&:hover {
				background-image: linear-gradient(to bottom, #51be77, #27ae58);
				p {
					color: #fff;
				}
			}
			p {
				color: #00994e;
				padding-left: 5px;
			}
			.iconSpan {
				&:before {
					content: '';
					margin: 5px 5px 5px 8px;
					width: 35px !important;
					height: 35px !important;
					position: absolute;
					background-size: contain;
				}
			}
		}
	}
	@media only screen and (min-width: 1280px) {
		.extra-nav-link {
			width: 100%;
			p {
				padding-left: 5px;
				font-size: 15px;
			}
			.iconSpan {
				&:before {
					content: '';
					margin: 5px 5px 5px 10px;
					position: absolute;
					background-size: contain;
				}
			}
		}
	}

	@keyframes fade-in {
		0%{transform: translateX(-100vw);} 
		100% { transform: translateX(0vw);}
	}
	@keyframes fade-out {
		0%{ transform: translateX(0vw);} 
		100%{ transform: translateX(-100vw);} 
	}

	.navbar{
		padding:0;
		@include make-navbar-default();

		.navbar-mega-menu{
			transition: all .3s;
			animation: fade-out .25s ease both;
		}
		.menu-on & .navbar-mega-menu{
			animation: fade-in .25s ease both;
		}


		@media (min-width: map-get($grid-breakpoints, 'lg')){
			.navbar-mega-menu{
				animation:none;
			}
			.menu-on & .navbar-mega-menu{
					animation:none;
			}
			&.sticky{
				position: fixed;
				width:100%;
				transform: translateY(-80px);
				
				.navbar-mega-menu{
					background-color: #00994e;
					.navbar-nav:not(.nav-unit-menu){
						.mega-dropdown{
							.nav-link{
								color:#fff;
								&:before{
									top:0;
								}

							}
							&:hover{
								.nav-link{
									opacity:1;
									background-color: #fff;
									color:#00994e;
								}
							}
						}

						.nav-link-green{
							background-image: linear-gradient(to bottom, #ffa81a, #ffa81a);
						}
					}
				}
			}
		}
	}

    .navbar-brand{
		@include make-navbar-brand()
		// @media (max-width:320px){
		// 	width: 30px
		// }
    }   

	.navbar-top-menu{
		@include make-navbar-top-menu();
		width:100%;

		position:fixed;
		top:0;
		left:0;
		z-index: 1002;

		.navbar-toggler{
			position: absolute;
			left:0.5rem;
			top: 10px;
		}

	}

	.nav-unit-menu{
		@include  make-navbar-unit-menu()
		.nav-link{
			display: inline;
	    	&:before{
				width: 0;
			    content: '';
			    position: absolute;
			    border-style: solid;
			    border-width: 0px 8px 8px 8px;
			    bottom: -30px;
			    left: 35px;
			    border-color: transparent transparent #00994f transparent;
	    	}
		}
	}

	.navbar-mega-menu{

		@include make-navbar-mega-menu();

		.nav-item{
			width: 100%;
			padding:0;
			box-sizing: border-box;
		}
		.nav-link {
		    letter-spacing: 1px;
		    display: flex;
		    align-items: center;
		    text-decoration: none;
		    position: relative;
		}


	    @media (max-width: map-get($grid-breakpoints, 'lg')){
			.navbar-nav:not(.nav-unit-menu) .nav-link{
				&:after{
					content:'';
					display:block;
					position:absolute;
					width:10px;
					height:10px;
					top:0;
					bottom:0;
					left:auto;
					margin-top:auto;
					margin-bottom:auto;
					border-bottom:0;

					border-top: 2px solid #777;
					border-right: 2px solid #777;
					transform: rotate(45deg) skew(0deg);
					right: 15px;
				}
				&[aria-expanded=true]{
					&:after{
						transform: rotate(135deg);
					}
				}
			}
			.mega-dropdown-menu-inner .dropdown-title{
				position:relative;
				&:after{
					content:'';
					display:block;
					position:absolute;
					width:10px;
					height:10px;
					top:0;
					bottom:0;
					left:auto;
					margin-top:auto;
					margin-bottom:auto;
					border-bottom:0;

					border-top: 2px solid #777;
					border-right: 2px solid #777;
					transform: rotate(45deg) skew(0deg);
					right: 15px;
				}
				&[aria-expanded=true]{
					&:after{
						transform: rotate(135deg);
					}
				}
			}
		}
		.nav-link-green{
			display: flex;
			align-items: center;
			justify-content: flex-start;
			letter-spacing: 1px;
			padding: 20px 50px 20px 30px;
			text-decoration: none;
			text-align: center;
			color:#333;
			text-decoration: none;
			i{
				margin-left:.25rem;
				margin-right:.4rem;
				font-size: 17px;
			}
			&:hover{
				color:#00994e;
			}
			&:after{
			 	display: none !important;
			}
		}
	    .img-icon{
			height:25px;
			width:25px;
			min-width:25px;
			margin-right: .3rem;
			background-size: 100% 100%;
			vertical-align: middle;
		}
		
		.extra-nav-link{
			.img-icon {
				width: 35px;
				height: 35px;
				min-width: 35px;
				min-height: 35px;
				margin: 5px 0px;
				display: block;
			}
		}
	    @media (min-width: map-get($grid-breakpoints, 'lg')){
			.nav-link-green{
				color:#fff;
				background-image: linear-gradient(to bottom, #51be77, #27ae58);
				&:hover{
					color:#fff;
					background-image: linear-gradient(to bottom, #ffa81a, #ffa81a);
				}
			}
	    }
	}

	.mega-dropdown{
		@include make-navbar-mega-dropdown()



		.dropdown-submenu{
			>.dropdown-item{
				white-space: normal;
				line-height: 1.3
			}
		}
		.mega-dropdown-menu-action{
			.img-icon{
				width: 35px;
			    height: 35px;
			    margin-right: .5rem
			}
		}

		.nav-link{
			&:before{
				content: "";
				display: none;
			    border-top: 4px solid #ffa81a;
			    position: absolute;
			    width: 100%;
			    top: 0px;
			    left:0;
			    border-radius: 0;
			}

			&:after{
				content:"";
				display: none;
				position: absolute;
				border:none;
			    border-bottom: 8px solid #fff;
			    left:0;
			    width: 100%;
			    bottom: 0;
			    z-index: 1001;
			    margin:0;
			}
		}

		.dropdown-toggle{
			position: relative;
		}

	}

	.navbar-mega-menu{

		@media (max-width: map-get($grid-breakpoints, 'lg')){
			.mega-dropdown-menu-inner ul > li:first-child .dropdown-item{
				padding-top:8px;
			}
		}
		@media (min-width: map-get($grid-breakpoints, 'lg')){
			.navbar-nav{
				.nav-link-green{
					justify-content: center;
					height: 47px;
					padding: 10px;
					text-align: center;
					color:#fff;
					text-decoration: none;
					background-image: linear-gradient(to bottom, #51be77, #27ae58);
					&:hover{
						color:#fff;
						background-image: linear-gradient(to bottom, #ffa81a, #ffa81a);
					}
				}
			}
			.nav-unit-menu{
				.nav-item{
				    padding:0 15px;
				    position: relative;
				    a{
				    	text-decoration: none;
			    		color: #444;
			    		font-size: 14px;
				    }
			    	&:before{
			    		display: none;
						width: 0;
					    content: '';
					    position: absolute;
					    border-style: solid;
					    border-width: 0px 8px 8px 8px;
					    bottom: -25px;
					    left: 35px;
					    border-color: transparent transparent #00994f transparent;
			    	}
				    &.active,&:hover{
			    		a{color: #11a847;}
				    }
				    &.active{
				    	&:before{
							display: block;
						}
				    }
				}
			}
		}

		
	}




}

