@mixin make-navbar-default( $object: $navbar-default, $breakpoints: $grid-breakpoints) {
  	@each $breakpoint, $object in $navbar-default {
	    @include media-breakpoint-up($breakpoint, $breakpoints) {
	    	display: map-get($object,display);
	    	flex-direction: map-get($object,flexDirection); 
	    	box-shadow: map-get($object,boxShadow); 
	    }
	}
}




@mixin make-navbar-brand( $object: $navbar-brand, $breakpoints: $grid-breakpoints) {
  	@each $breakpoint, $object in $navbar-brand {
	    @include media-breakpoint-up($breakpoint, $breakpoints) {
	    	height: map-get($object,height);
	    	width: map-get($object,width); 
	    	background-image: map-get($object,backgroundImage); 
	    	background-size: map-get($object,backgroundSize); 
	    	margin-right: map-get($object,marginRight); 
	    	margin-left: map-get($object,marginLeft); 
	    }
	}
}

@mixin make-navbar-top-menu( $object: $navbar-top-menu, $breakpoints: $grid-breakpoints) {
  	@each $breakpoint, $object in $navbar-top-menu {
	    @include media-breakpoint-up($breakpoint, $breakpoints) {
	    	z-index:map-get($object,zIndex);
	    	position:map-get($object,position);
	    	top:map-get($object,top);
	    	left:map-get($object,left);
	    	height: map-get($object,height);
	    	background-color:map-get($object,bgColor);
	    	border-bottom:1px solid map-get($object,borderBottomColor);

	    	$InsideContainer: map-get($object,InsideContainer);

	    	>.container{
	    		display: map-get($InsideContainer,display);
	    		justify-content:  map-get($InsideContainer,justifyContent);
	    		align-items:  map-get($InsideContainer,alignItems);
	    		height:  map-get($InsideContainer,height);
	    	}

	    	$UnitMenu: map-get($object,UnitMenu);

	    	.nav-unit-menu{
	    		display: map-get($UnitMenu,display);
	    	}

	    }
	}
}

@mixin make-navbar-mega-menu( $object: $navbar-mega-menu, $breakpoints: $grid-breakpoints) {
  	@each $breakpoint, $object in $navbar-mega-menu {
	    @include media-breakpoint-up($breakpoint, $breakpoints) {
	    	// min-height: map-get($object,minHeight);
	    	// max-height: map-get($object,maxHeight);
	    	height: map-get($object,height);
	    	width:map-get($object,width);
	    	overflow-y:map-get($object,overflowY);
	    	background-color:map-get($object,bgColor);
	    	border-right:map-get($object,borderRight);
	    	display:map-get($object,display);
	    	padding:map-get($object,padding);
	    	z-index:map-get($object,zIndex);
	    	position:map-get($object,position);
	    	top:map-get($object,top);
	    	left:map-get($object,left);
	    	$InsideContainer: map-get($object,InsideContainer);

	    	>.container{
	    		display: map-get($InsideContainer,display);
	    		flex-direction: map-get($InsideContainer,flexDirection);
	    		justify-content:  map-get($InsideContainer,justifyContent);
	    		align-items:  map-get($InsideContainer,alignItems);
	    		padding: map-get($InsideContainer,padding);
	    		position: map-get($InsideContainer,position);
	    	}

	    	$UnitMenu: map-get($object,UnitMenu);

	    	.nav-unit-menu{
	    		display: map-get($UnitMenu,display);
	    	}

			.navbar-nav{
				width: map-get($object,NavBarWidth);
				background-color:map-get($object,NavBarBgColor);
			}

	    	.navbar-nav:not(.nav-unit-menu){

		    	$navItem: map-get($object,navItem);
		    	.nav-item{
		    		border-bottom: map-get($navItem,borderBottom);
		    		border-left: map-get($navItem,borderLeft);
		    		max-width: map-get($navItem,maxWidth);
		    	}
		    	.nav-item--last{
		    		border-right: map-get($navItem,borderLeft);
		    	}

		    	$navlink: map-get($object,navlink);
		    	.nav-link{
		    		color: map-get($navlink,color);
		    		height: map-get($navlink,height);
		    		padding: map-get($navlink,padding);
		    		justify-content: map-get($navlink,justifyContent);
		    		&:hover,&:focus{
		    			color: map-get($navlink,hoverColor);
		    		}
		    	}
	    	}


	    }
	}
}

@mixin make-navbar-unit-menu( $object: $navbar-unit-menu, $breakpoints: $grid-breakpoints) {
  	@each $breakpoint, $object in $navbar-unit-menu {
	    @include media-breakpoint-up($breakpoint, $breakpoints) {

	    	$navItem:map-get($object,navItem);
	    	$navlink:map-get($object,navlink);

	    	padding:map-get($object,padding);

	    	.nav-item{

	    		&.active{
	    			.nav-link{
	    				color: map-get($navlink,active-color);
		    			&:before{
		    				display: map-get($navlink,active-arrows);
		    			}
	    			}
	    		}

	    	}

	    	.nav-link{
	    		position: map-get($navlink,position);
	    		padding: map-get($navlink,padding);
	    		font-size: map-get($navlink,fontSize);
	    		height: map-get($navlink,height);
	    		color: map-get($navlink,color);
	    		justify-content: map-get($navlink,justifyContent);
	    		&:hover,&:focus{
	    			color: map-get($navlink,hover-color);
	    		}
    			&:before{
    				display: map-get($navlink,arrows);
    			}
	    	}

	    }
	}
}

@mixin make-navbar-mega-dropdown( $object: $navbar-mega-dropdown, $breakpoints: $grid-breakpoints) {
  	@each $breakpoint, $object in $navbar-mega-dropdown {
	    @include media-breakpoint-up($breakpoint, $breakpoints) {

	    	$container:map-get($object,container);
	    	$dropdownMenu:map-get($object,dropdownMenu);
	    	$dropdownMenuInner:map-get($object,dropdownMenuInner);
	    	$dropdownMenuAction:map-get($object,dropdownMenuAction);


    		&:active,&:hover,&:focus{
				//border-top:map-get($object,hover-borderTop);
				//margin-top:map-get($object,hover-marginTop);
				.nav-link{
					&:before{display:map-get($object,hover-before);}
	    			&:after{display:map-get($object,hover-after);}
				}



			}

	    	.container{

	    		display: map-get($container,display);
	    		flex-direction: map-get($container,flexDirection);

	    	}


	    	.mega-dropdown-menu{

	    		$container:map-get($dropdownMenu,container);

	    		position:map-get($dropdownMenu,position);
	    		border:map-get($dropdownMenu,border);
	    		padding:map-get($dropdownMenu,padding);
	    		padding-left:map-get($dropdownMenu,paddingLeft);
	    		background-image:map-get($dropdownMenu,backgroundImage);
	    		box-shadow:map-get($dropdownMenu,boxShadow);
	    		//display:map-get($dropdownMenu,display);

	    		>.container{

	    			$container:map-get($dropdownMenu,container);
	    			padding:map-get($container,padding);
	    		}
	    		&.show{
	    			display:block;
	    		}
	    	}

	    	.mega-dropdown-menu-inner{

	    		$li:map-get($dropdownMenuInner,li);
	    		
	    		display: map-get($dropdownMenuInner,display);
	    		flex:map-get($dropdownMenuInner,flex);
	    		background-image: map-get($dropdownMenuInner,backgroundImage);
	    		width: map-get($dropdownMenuInner,width);
				padding: map-get($dropdownMenuInner,padding);
	    		>li{
	    			width: map-get($li,width);
	    			max-width: map-get($li,maxWidth);
	    			padding:  map-get($li,padding);
	    			margin: map-get($li,margin);
	    			// border-bottom:map-get($li,borderBottom);

	    			&.extra-navbar{
	    				// padding-right:map-get($li,extraNavbarPaddingRight);
						margin-left:map-get($li,extraNavbarMarginLeft);
						min-width: 200px;;
	    			}
	    			+li{
						border-top:map-get($li,borderTop);
	    			}
	    		}



    			&.mega-dropdown-menu-inner-length-1>li{
    				width: map-get($li,width1);
    			}

    			&.mega-dropdown-menu-inner-length-2>li{
    				width: map-get($li,width2);
    			}

    			&.mega-dropdown-menu-inner-length-3>li{
    				width: map-get($li,width3);
    			}

    			&.mega-dropdown-menu-inner-length-4>li{
    				width: map-get($li,width5);
    			}

    			&.mega-dropdown-menu-inner-length-5>li{
    				width: map-get($li,width5);
    				
    			}

    			&.mega-dropdown-menu-inner-length-6>li{
    				width: map-get($li,width6);
    				
    			}

    			&.mega-dropdown-menu-inner-length-7>li{
    				
    			}
	    		.dropdown-submenu{
		    		$dropdownSubmenu:map-get($dropdownMenuInner,dropdownSubmenu);
		    		margin: map-get($dropdownSubmenu,margin);
	    		}
		    	ul{

		    		$dropdownSubmenu:map-get($dropdownMenuInner,dropdownSubmenu);
		    		$dropdownItem:map-get($dropdownSubmenu,dropdownItem);

	    			display: map-get($dropdownSubmenu,display);
		    		visibility: map-get($dropdownSubmenu,visibility);
		    		opacity: map-get($dropdownSubmenu,opacity);
		    		border:map-get($dropdownSubmenu,border);
		    		padding: map-get($dropdownSubmenu,padding);
		    		background-image:map-get($dropdownSubmenu,backgroundImage);
		    		background-color:map-get($dropdownSubmenu,backgroundColor);
		    		border-bottom:map-get($dropdownSubmenu,borderBottom);
		    		transform:map-get($dropdownSubmenu,transform);
		    		position:map-get($dropdownSubmenu,position);

		    		
		    		>li{
		    			padding:map-get($dropdownItem,padding);
		    			font-size:map-get($dropdownItem,fontSize);
		    			&:first-child{
		    				padding-top:0;
		    				.dropdown-item{
		    					padding-top:map-get($dropdownItem,first-paddingTop);
		    				}
		    			}
		    			&:last-child{
		    				.dropdown-item{
		    					padding-bottom:map-get($dropdownItem,last-paddingBottom);
		    				}
		    			}
		    			+li{
		    				margin-top:14px;
		    			}
		    		}

		    		.dropdown-item{
		    			font-size: map-get($dropdownItem,fontSize);
		    			padding: map-get($dropdownItem,padding);
		    			&:active,&:hover,&:focus{
		    				color:map-get($dropdownItem,hover-color);
		    				background-color:map-get($dropdownItem,hover-background);
		    			}
		    		}

		    		&.show{
		    			visibility: visible;
		    			display:block;
		    		}
		    	}

		    	$dropdownTitle:map-get($dropdownMenuInner,dropdownTitle);
		    	$dropdownItem:map-get($dropdownMenuInner,dropdownItem);

		    	.dropdown-title{
		    		text-decoration:map-get($dropdownTitle,textDecoration);
		    		display:map-get($dropdownTitle,display);
		    		white-space:map-get($dropdownTitle,whiteSpace);
		    		line-height:map-get($dropdownTitle,lineHeight);
		    		letter-spacing:map-get($dropdownTitle,letterSpacing);
		    		padding:map-get($dropdownTitle,padding);
		    		margin-bottom:map-get($dropdownTitle,marginBottom);
		    		color:map-get($dropdownTitle,color);
		    		border-bottom:map-get($dropdownTitle,borderBottom);
		    		font-size:map-get($dropdownTitle,fontSize);
		    	}

		    	.dropdown-item{

		    		text-decoration:map-get($dropdownItem,textDecoration);
		    		font-size:map-get($dropdownItem,fontSize);


		    	}
	    	}

	    	.mega-dropdown-menu-action{

	    		$li:map-get($dropdownMenuAction,li);
	    		$dropdownItem:map-get($dropdownMenuAction,dropdownItem);

	    		width: map-get($dropdownMenuAction,width);
	    		display: map-get($dropdownMenuAction,display);
	    		flex-direction: map-get($dropdownMenuAction,flexDirection);

	    		>li{
	    			display: map-get($li,display);
	    			flex:map-get($li,flex);
	    			justify-content:  map-get($li,justifyContent);
	    			align-items: map-get($li,alignItems);
	    			padding: map-get($li,padding);
	    			&:last-child{
		    			.dropdown-item{border-bottom:0;}
		    		}
	    		}

	    		.dropdown-item{
	    			text-decoration: map-get($dropdownItem,textDecoration);
	    			font-size: map-get($dropdownItem,fontSize);
	    			color:map-get($dropdownItem,color);
		    		display: map-get($dropdownItem,display);
		    		height: map-get($dropdownItem,height);
		    		padding: map-get($dropdownItem,padding);
		    		align-items: map-get($dropdownItem,alignItems);
		    		background-color:map-get($dropdownItem,backgroundColor);
		    		border-radius:map-get($dropdownItem,borderRaduis);
		    		border-bottom:map-get($dropdownItem,borderBottom);

		    		&:hover{
		    			color:map-get($dropdownItem,hover-color);
		    			background-image:map-get($dropdownItem,hover-backgroundImage);
		    		}
	    		}

	    	}

	    }
	}
}

